import React, { useEffect, useState, useCallback } from 'react'
import {
    // CircularProgress,
} from '@mui/material'
import { useNavigate } from 'react-router-dom'
import './Clients.css'
import moment from 'moment';
import useAuth from 'app/hooks/useAuth';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import UsersCustomTable from 'app/components/UsersCustomTable/UsersCustomTable';
import { ProfileCard, Breadcrumb, SearchInput, UserProfileDetail } from 'app/components';

import { fetchClient } from 'app/services/clientService'
import NotesModal from 'app/components/CustomModal/NotesModal'
import { tabsData, tabsCounselorData } from 'app/constants/data';
import AppointmentListTable from 'app/components/AppointmentListTable/AppointmentListTable';
import { formatCenters } from '../../../utils/data.formater'
import { checkTimeDiffBetween } from 'app/utils/utils';
import { formatSession } from '../../../utils/counseler.data.formater';
import SessionModal from 'app/components/SessionModal/SessionModal';
import { styled } from '@mui/system';
import '../../users/Users.css';

const Container = styled('div')(({ theme }) => ({
    margin: '30px',
    [theme.breakpoints.down('sm')]: {
        margin: '16px',
    },
    '& .breadcrumb': {
        marginBottom: '30px',
        [theme.breakpoints.down('sm')]: {
            marginBottom: '16px',
        },
    },
}))

// const StyledProgress = styled(CircularProgress)(() => ({
//     position: 'absolute',
//     top: '6px',
//     left: '25px',
// }))

const ClientDetails = () => {
    const [loading, setLoading] = useState(false)
    const [value, setValue] = useState('1');
    const [modalVisible, setModalVisible] = useState(false);
    const [valueAppointmentTab, setValueAppointmentTab] = useState('1');
    const [tab, setTab] = useState([])
    const [sessionId, setSessionId] = useState([])
    const [tabAppointment, setTabAppointment] = useState([
        {
            id: 1,
            name: 'Upcoming'
        },
        {
            id: 2,
            name: 'History'
        }
    ])
    const [detailsData, setDetailsData] = useState('')
    const [centerData, setCenterData] = useState('')
    const [clientData, setClientData] = useState('')
    const [appointmentHistoryData, setAppointmentHistoryData] = useState('')
    const [appointmentUpcomingData, setAppointmentUpcomingData] = useState('')
    const [rowsPerPage, setRowsPerPage] = React.useState(5)
    const [page, setPage] = React.useState(0)
    const [searchResult, setSearchResult] = useState(false)
    const [logo, setLogo] = useState('')
    const [searchedData, setSearchedData] = useState({
        center: [],
        counsellor: [],
        client: [],
        appointment: [],
    })
    const [values, setValues] = useState({
        center: '',
        counsellor: '',
        client: '',
        appointment: '',
    })
    const [openModal, setOpenModal] = useState(false);
    const [msg, setMsg] = useState('');
    const navigate = useNavigate();
    const { user } = useAuth();
    const id = window.location.href.split('/').pop();

    //Edit button function
    const [centerIds, setCenterIds] = useState([])

    const handleChangePage = (event, newPage) => {
        setPage(newPage)
    }

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value)
        setPage(0)
    }

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };
    const handleAppointmentTabChanged = (e, newValue) => {
        setValueAppointmentTab(newValue)
    }

    const handleBlur = (prop) => (e) => {
        let value = e.target.value
        setValues({ ...values, [prop]: value })
        if (value && value.length > 2) {
            let searchData
            if (prop && prop == 'center') {
                searchData = centerData.filter((item) => {
                    return item.name.toLowerCase().includes(value) || item.email?.toLowerCase().includes(value) || item.phoneNumber?.toString().toLowerCase().includes(value)
                })
                searchData['meta'] = centerData.meta
            } else if (prop && prop == 'client') {
                searchData = clientData.filter((item) => {
                    return item.name.toLowerCase().includes(value) || item.status.toLowerCase().includes(value) || item.gender.toLowerCase().includes(value)
                })
                searchData['meta'] = clientData.meta
            } else if (prop && prop == 'appointment') {
                searchData = appointmentUpcomingData.filter((item) => {
                    return item.caseId.includes(value) || item.sessionId?.includes(value) || item.status.toLowerCase().includes(value)
                })
                searchData['meta'] = appointmentUpcomingData.meta
            }

            setSearchResult(true)
            setSearchedData({ ...searchedData, [prop]: searchData })
        } if (value.length === 0) {
            setSearchResult(false)
        }
    }

    const handleNotes = (id, visible) => {
        setSessionId(id);
        setModalVisible(visible);
    }

    const fetchData = async () => {
        let id = window.location.href.split('/').pop();
        const res = await fetchClient(id)
        return res
    }

    function formatDetails(data) {

        const response = {
            clientId: data.client_id,
            name: data.name ? data.name : '-',
            genderBio: data.gender_bio ? data.gender_bio : '-',
            genderIdentified: data.gender_identified ? data.gender_identified : '-',
            initialDiagnosis: data.initial_diagnosis ? data.initial_diagnosis.name : '-',
            centerName: data.center ? data.center.name : '-',
            date_created: moment(data.createdAt).format('DD MMMM, YYYY'),
        };

        return response
    }

    const makeAppointmentData = (data) => {
        let upcomingAppointment = []
        let historyAppointment = []
        const td_start = moment().startOf('minute')
        const td_end = moment().endOf('second')
        data.length > 0 && data.map((item) => {
            if (moment(moment(item.scheduled_at).format('x'), 'x').isAfter(td_start)) {
                const resp = item.status === 'pending' && checkTimeDiffBetween(item, data) || false
                item.disabled = resp.length > 0 ? true : false
                upcomingAppointment.push(item)
            } else {
                historyAppointment.push(item)
            }
        })
        setAppointmentUpcomingData(formatSession(upcomingAppointment, 'upcoming'))
        setAppointmentHistoryData(formatSession(historyAppointment, 'history'))

    }
    useEffect(() => {

        fetchData(id).then((res) => {
            if (res) {
                if(res?.error?.name === 'UnauthorizedError'){
                    setOpenModal(true);
                    setMsg('Your session has expired. You will be logged out.');
                }
                // setCounsellorData(formatCounsellor(res.counsellor))
                setCenterData(formatCenters(res.center))
                // setClientData(formatClient(res.clients))
                // if (moment(moment(item.scheduled_at).format('x'), 'x').isBetween(td_start, td_end)) {
                //     data.push(item)
                // }
                setDetailsData(formatDetails(res))
                makeAppointmentData(res.sessions)
            }
            if (res.logo_image) {
                let logo = 'http://localhost:1337' + res.logo_image
                setLogo(logo)
            }
            setLoading(false)
        })
            .catch(console.error)
    }, [])

    useEffect(() => {
        setLoading(true)
        if (centerData) {
            const ids = centerData.map((item) => {
                return item.id
            })
            console.log(ids)
            setCenterIds(ids)
        }
        setTab(tabsCounselorData.client)

    }, [detailsData, centerData, tab])

    const Center = () => {
        return <>
            <SearchInput placeholder="Search Center" value={values.center} handleBlur={handleBlur('center')} />
            <UsersCustomTable role="Center" rowsPerPage={rowsPerPage} page={page} handleChangeRowsPerPage={handleChangeRowsPerPage} handleChangePage={handleChangePage} rows={searchResult && values.center !== '' ? searchedData.center : centerData} />
        </>
    }
    const Client = () => {
        return <>
            <SearchInput placeholder="Search Client" value={values.client} handleBlur={handleBlur('client')} />
            <UsersCustomTable role="Client" rowsPerPage={rowsPerPage} page={page} handleChangeRowsPerPage={handleChangeRowsPerPage} handleChangePage={handleChangePage} rows={searchResult && values.client !== '' ? searchedData.client : clientData} />
        </>
    }
    const UpcomingAppointments = () => {
        return <>
            {/* <SearchInput placeholder="Search Appointment" value={values.appointment} handleBlur={handleBlur('appointment')} /> */}
            {/* <UsersCustomTable role="Session" rowsPerPage={rowsPerPage} page={page} handleChangeRowsPerPage={handleChangeRowsPerPage} handleChangePage={handleChangePage} rows={searchResult && values.appointment !== '' ? searchedData.appointment : appointmentUpcomingData} /> */}
            {appointmentUpcomingData &&
                <AppointmentListTable
                    role={user.role.name}
                    data={appointmentUpcomingData}
                    loading={loading}
                    fetchData={fetchData}
                    user={user}
                />
            }
        </>
    }

    const HistoryAppointments = () => {
        return <>
            {appointmentUpcomingData &&
                <AppointmentListTable
                    role={user.role.name}
                    data={appointmentHistoryData}
                    loading={loading}
                    fetchData={fetchData}
                    user={user}
                />
            }
        </>
    }

    const Appointment = () => {
        return <>
            <Box sx={{ padding: 0, margin: 0 }}>
                <TabContext value={valueAppointmentTab}>
                    <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                        <TabList onChange={handleAppointmentTabChanged} aria-label="Appointment Details Segement">
                            {tabAppointment.map((item, index) => (
                                <Tab label={item.name} value={item.id.toString()} key={index} />
                            ))}
                        </TabList>
                    </Box>
                    {tabAppointment && tabAppointment.map((item, index) => {
                        return (
                            <TabPanel key={index} value={item.id.toString()}>
                                {item.id === 1 ? (
                                    <UpcomingAppointments />
                                ) : (
                                    <HistoryAppointments />
                                )}
                            </TabPanel>
                        )
                    })}

                </TabContext>
            </Box>
        </>
    }
    return <Container>
        <Breadcrumb
            routeSegments={[
                { name: "All Clients", path: "/clients-list" },
                { name: "Client Details" }
            ]}
        />
        {!loading && detailsData !== '' && (
            <TabContext value={value}>
                <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                    <TabList onChange={handleChange} aria-label="Client Details Segement">
                        {tab.map((item, index) => (
                            <Tab label={item.name} value={item.id.toString()} key={index} />
                        ))}
                    </TabList>
                </Box>
                {tab && tab.map((item, index) => {
                    return (
                        <TabPanel key={index} value={item.id.toString()}>
                            {item.id === 1 ? (
                                <ProfileCard
                                    title={detailsData.name}
                                    subtitle={"Client"}
                                    icon={logo}
                                    action={false}
                                // anchorEl={anchorEl}
                                // open={open}
                                // handleClick={handleClick}
                                // handleClose={handleClose}
                                // navigateTo={pageNavigate}
                                // assignCenter={assignCenter}
                                >
                                    <UserProfileDetail data={detailsData} />
                                </ProfileCard>
                            ) : item.id === 2 ? (
                                <Center />
                            ) : item.id === 4 ? (
                                <Client />
                            ) : (
                                <Appointment />
                            )}
                        </TabPanel>
                    )
                })}
                <NotesModal
                    open={modalVisible}
                    data={sessionId}
                    multipleSession={false}
                    handleClose={() => {
                        handleNotes(null, false);
                        //handlePageChange(null, 0);
                    }}
                />
                <SessionModal
                    open={openModal}
                    text={msg}
                    onConfirmDialogClose={() => {
                        setMsg('');
                        setOpenModal(false);
                    }}
                    onYesClick={() => {
                        setMsg('');
                        setOpenModal(false);
                    }}
                />
            </TabContext>

        )}

    </Container>
}

export default ClientDetails